@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,400;0,700;1,700&display=swap);
#about {
    height: 100vh;
    padding-top: 2rem;
}

#about h2 {
    text-align: center;
    font-size: 1.8rem;
}

#about p {
    width: 50%;
    margin: 0 auto;
    text-align: center;
    padding-top: 4rem;
    font-size: 1.4rem;
}

.technologies {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding-top: 12rem;
}

.technologies p {
    padding-top: 0.5rem !important;
    font-weight: 900;
}

.technologie img {
    width: 100px;
    object-fit: cover;
}

.technologie img:hover {
    transform: scale(1.2);
    transition: transform 1s ease-in-out;
}

@media (max-width: 768px) {
    #about p {
        width: 90%;
    }

    .technologies {
        flex-direction: column;
        align-items: center;
        padding-top: 2rem;
    }

    .technologie {
        padding-top: 2rem;
    }
}

#contacts {
    height: 100vh;
    padding-top: 2rem;
    background-color: rgba(0, 0, 0, 0.8);
}

#contacts h2 {
    text-align: center;
    color: white;
}

.contact__info p {
    text-align: center;
    padding-top: 2rem;
    color: white;
    font-size: 1.2rem;
}

header {
    width: 100%;
    height: 100vh;
    background-color: #8bc6ec;
    background-image: linear-gradient(135deg, #8bc6ec 0%, #9599e2 100%);
}

.bars {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    padding-top: 1rem;
}

.bars a {
    text-decoration: none;
}

.bars a p {
    font-size: 2rem;
    color: black;
}

.navigation ul {
    display: flex;
}

.navigation ul li {
    list-style: none;
}

.navigation ul li a {
    text-decoration: none;
    font-size: 1.4rem;
    color: black;
    margin: 0 1rem;
}

.navigation ul li a:hover {
    border-bottom: 2px solid black;
}

.line {
    width: 28px;
    height: 4px;
    margin: 4px;
    background-color: black;
    border-radius: 8px;
}

.burger {
    display: none;
}

@media (max-width: 768px) {
    body {
        overflow-x: hidden;
    }

    .bars {
        width: 98%;
    }

    .burger {
        display: block;
        cursor: pointer;
    }

    .navigation {
        position: absolute;
        top: 8vh;
        right: 0;
        height: calc(100% - 8vh);
        width: 100%;
        background-color: rgba(0, 0, 0, 0.8);
        transform: translateX(100%);
        transition: transform 1s ease-in-out;
    }

    .navigation ul {
        height: 100%;
        flex-direction: column;
        justify-content: space-around;
        text-align: center;
    }

    .navigation ul li a {
        color: white;
    }

    .menu__active {
        transform: translateX(0);
        transition: transform 1s ease-in-out;
    }
}

.info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 14rem;
}

.info img {
    width: 200px;
    object-fit: cover;
    border-radius: 50%;
    margin-bottom: 1rem;
    border: 4px solid black;
}

#projects {
    height: 100vh !important;
    display: flex;
    justify-content: center;
    align-items: center;
}

#projects h3 {
    color: black;
    font-size: 2rem;
}

* {
    margin: 0;
    padding: 0;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

body,
html {
    font-family: "Ubuntu", sans-serif;
    scroll-behavior: smooth;
}

