.bars {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    padding-top: 1rem;
}

.bars a {
    text-decoration: none;
}

.bars a p {
    font-size: 2rem;
    color: black;
}

.navigation ul {
    display: flex;
}

.navigation ul li {
    list-style: none;
}

.navigation ul li a {
    text-decoration: none;
    font-size: 1.4rem;
    color: black;
    margin: 0 1rem;
}

.navigation ul li a:hover {
    border-bottom: 2px solid black;
}

.line {
    width: 28px;
    height: 4px;
    margin: 4px;
    background-color: black;
    border-radius: 8px;
}

.burger {
    display: none;
}

@media (max-width: 768px) {
    body {
        overflow-x: hidden;
    }

    .bars {
        width: 98%;
    }

    .burger {
        display: block;
        cursor: pointer;
    }

    .navigation {
        position: absolute;
        top: 8vh;
        right: 0;
        height: calc(100% - 8vh);
        width: 100%;
        background-color: rgba(0, 0, 0, 0.8);
        transform: translateX(100%);
        transition: transform 1s ease-in-out;
    }

    .navigation ul {
        height: 100%;
        flex-direction: column;
        justify-content: space-around;
        text-align: center;
    }

    .navigation ul li a {
        color: white;
    }

    .menu__active {
        transform: translateX(0);
        transition: transform 1s ease-in-out;
    }
}
