#about {
    height: 100vh;
    padding-top: 2rem;
}

#about h2 {
    text-align: center;
    font-size: 1.8rem;
}

#about p {
    width: 50%;
    margin: 0 auto;
    text-align: center;
    padding-top: 4rem;
    font-size: 1.4rem;
}

.technologies {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding-top: 12rem;
}

.technologies p {
    padding-top: 0.5rem !important;
    font-weight: 900;
}

.technologie img {
    width: 100px;
    object-fit: cover;
}

.technologie img:hover {
    transform: scale(1.2);
    transition: transform 1s ease-in-out;
}

@media (max-width: 768px) {
    #about p {
        width: 90%;
    }

    .technologies {
        flex-direction: column;
        align-items: center;
        padding-top: 2rem;
    }

    .technologie {
        padding-top: 2rem;
    }
}
